import { ImagePreview } from 'vant'
import { batchList } from '@/api/common'
import { collegeCourse, collegeNews} from "@/api/college"
import config from "@/config/common.config"
import SwipeBanner from '@/components/Common/SwipeBanner'
import CourseCard from '@/components/Common/CourseCard2'
import NewsCard from '@/components/Common/NewsCard'
import ThinkTankCard from '@/components/college/ThinkTankCard'
import qrcode1 from '@/assets/images/college/icon_5.png'
import qrcode2 from '@/assets/images/college/icon_8.png'
import qrcode3 from '@/assets/images/college/icon_9.png'
export default {
  components:{
    SwipeBanner,
    CourseCard,
    NewsCard,
    ThinkTankCard
  },
  async asyncData () {
    const courseData = await collegeCourse({size:8})
    const collegeNewsData = await collegeNews({
      id: 32,
      info: {  page:1, pageSize:4 }
    })
    if (courseData.data.code === 0 && collegeNewsData.data.code === 0) {
      return {courseData:courseData.data.data, collegeNewsData:collegeNewsData.data.data.entries}
    }
  },
  data() {
    return {
      config,
      qrcode1,
      qrcode2,
      qrcode3,
      banner: [],
      thinkTankData: [
        { icon: require('@/assets/images/college/icon_12.png'), name: 'David Bassir', liList: [{text: '广州中国科学院'}, {text: ' 工业技术研究院专家'}, {text: '交通运输学科带头人'}] },
        { icon: require('@/assets/images/college/icon_13.png'), name: '梁安辉 教授', liList: [{text: '美籍华人'}, {text: ' 山东科技大学教授'}, {text: '光电工程学科带头人'}] },
        { icon: require('@/assets/images/college/icon_14.png'), name: '曾庆生 教授', liList: [{text: '加拿大籍华人'}, {text: ' 加拿大政府通信研究中心研究院信息通信学科带头人'}] },
        { icon: require('@/assets/images/college/icon_15.png'), name: '杨俊 教授', liList: [{text: '北京林业大学教授/博导'}] },
        { icon: require('@/assets/images/college/icon_16.png'), name: '张德富 教授', liList: [{text: '厦门“双百计划”'}, {text: ' 领军型创业人才'}, {text: '中国大数据学术创新百人'}] },
        { icon: require('@/assets/images/college/icon_17.png'), name: 'Patrice Salzenstein', liList: [{text: '法国CNRS研究所高级研究员'}, {text: ' 机电学科带头人'}] },
        { icon: require('@/assets/images/college/icon_18.png'), name: 'Zied Moumni', liList: [{text: '上海交通大学机械工程系'}, {text: ' 首席科学家'}, {text: '材料学科带头人'}] },
        { icon: require('@/assets/images/college/icon_19.png'), name: 'Stuart Perrin', liList: [{text: '西交利物浦大学先进教育学院院长'}, {text: ' 教育学学科带头人'}] },
      ]
    }
  },
  mounted () {
    this.getbatchList()
  },
  methods: {
    async getbatchList () {
      const { data } = await batchList('h5_gaoxiao')
      if (data.code === 0) {
        this.banner = data.data.h5_gaoxiao
      }
    },
    peerImg (src) {
      ImagePreview({
        images: [
          src
        ],
        showIndex: false,
        className: 'coolegeImg'
      })
    }
  },
  head () {
    return {
      title: '高校公益行-艾思云课堂',
    }
  },
}
